body {
  font: 12px/150% corbel, Helvetica, Arial, Helvetica, sans-serif;
  text-align: center;
  padding: 0;
  margin: 0 auto;
  position: relative;
}
body > div {
  text-align: left;
  margin-right: auto;
  margin-left: auto;
}
div,
form,
ol,
li,
span,
ul,
dl,
dt,
dd {
  margin: 0;
  padding: 0;
  border: 0;
  text-align: left;
}
img,
a img,
p {
  border: 0;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 12px;
}
ul,
ol,
li {
  list-style: none;
  text-align: left;
}
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
}
img {
  vertical-align: top;
}
/* 默认链接颜色 */
a {
  outline-style: none;
  text-decoration: none;
  color: #292929;
}
a:focus {
  outline: 0;
}
a:hover {
}
input,
button,
select,
textarea {
  outline: none;
}
/*添加语言选择模块功能*/
.languageWrap {
  width: 209px;
  position: absolute;
  right: 0px;
  top: 0;
  z-index: 39;
}
.languageWrap .default {
  display: block;
  width: 127px;
  padding-left: 20px;
  height: 34px;
  line-height: 34px;
  color: #6a6a6a;
  margin-left: 60px;
}
.default img {
  vertical-align: middle;
  margin-right: 12px;
  margin-left: 4px;
}
.default:hover,
.bgColorB:hover {
  color: #6a6a6a;
}
.bgColorB {
  background: #242424;
  border: 1px solid #535353;
  border-bottom: none;
}
.languageWrap ul {
  display: none;
  background: #242424;
  border: 1px solid #535353;
}
.languageWrap ul li {
  display: block;
  height: 34px;
}
.languageWrap ul li span {
  display: block;
  margin: 0 12px;
  border-bottom: 1px solid #535353;
  color: #c5c5c5;
  text-align: left;
  line-height: 34px;
}
.languageWrap ul li a {
  display: block;
  margin: 0 12px;
  border-bottom: 1px solid #535353;
  color: #c5c5c5;
  padding-left: 12px;
  text-align: left;
  line-height: 34px;
}
.languageWrap ul li a img {
  vertical-align: middle;
  margin-right: 12px;
}
