/*站内消息样式*/
.emailInfoWrap {
  width: 898px;
  background: #1a1a1a;
  border: 1px solid #666666;
  max-height: 558px;
  overflow: hidden;
  border-radius: 2px 2px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -280px 0 0 -450px;
  z-index: 1000;
  display: none;
}
.emailInfoWrap h1 {
  height: 70px;
  line-height: 70px;
  position: relative;
  padding-left: 25px;
  color: #fff;
  font-size: 20px;
  font-weight: bolder;
  background: #434343;
}
.messageClose {
  position: absolute;
  right: 30px;
  top: 15px;
  line-height: 40px;
  text-align: center;
  padding: 0 20px;
  font-size: 16px;
  font-weight: bolder;
  color: #fff;
  height: 40px;
  background: #333;
  border: 1px solid #797979;
  display: block;
}
.messageClose:hover {
  color: #fff;
}
.emailInfoScroll {
  height: 488px;
  max-height: 488px;
  overflow-y: scroll;
  background: #1a1a1a;
}
.emailInfoBox {
  background: #323232;
  margin: 20px 30px;
}
.emailInfoBox h2 {
  overflow: hidden;
  height: 50px;
  line-height: 50px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bolder;
  color: #fff;
  padding-left: 80px;
  padding-right: 80px;
  position: relative;
}
.emailInfoBox h2 span.emailNoRead {
  width: 25px;
  height: 25px;
  display: inline-block;
  position: absolute;
  left: 50px;
  top: 15px;
  background: url(data:image/png;base64,) 3px 3px no-repeat;
}
.emailInfoBox h2 span.emailRead {
  width: 25px;
  height: 25px;
  display: inline-block;
  position: absolute;
  left: 50px;
  top: 15px;
  background: url(data:image/png;base64,) 3px -48px no-repeat;
}
.emailInfoBox h2 a.show {
  width: 20px;
  height: 20px;
  line-height: 18px;
  text-align: center;
  border: 1px solid #634426;
  color: #ff7e00;
  display: inline-block;
  position: absolute;
  right: 20px;
  top: 10px;
}
.emailInfoBox .emailInfo {
  margin: 10px 45px;
  display: none;
}
.emailInfoBox .emailInfo .messageContent {
  color: #9a9a9a;
  word-break: break-all;
  line-height: 20px;
  padding: 0 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.emailInfoBox .emailInfo img {
  margin: 0 20px 20px;
}
.emailTime {
  height: 40px;
  line-height: 40px;
  text-align: right;
  color: #9a9a9a;
  padding: 0 40px;
}
/******遮罩样式******/
