/****footer 协议****/
#box,
#box1 {
  width: 1000px;
  height: 600px;
  border: 2px solid #cecccc;
  background: #fff;
  z-index: 11;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -500px;
  margin-top: -300px;
  display: none;
}
#paymentHistory {
  width: 1000px;
  border: 2px solid #000;
  background: #fff;
  z-index: 2;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -500px;
  margin-top: -300px;
  display: none;
}
#paymentHistory #closePaymentHistory,
#box #close,
#box1 #close1 {
  position: absolute;
  right: 20px;
  top: 10px;
  width: 35px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  background: #ccc;
  text-decoration: none;
  color: #353434;
  font-weight: bold;
  font-size: 22px;
  cursor: pointer;
}
#paymentHistory #title,
#box #title,
#box1 #title {
  position: absolute;
  left: 0;
  top: 0;
  width: 1000px;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  color: #222;
  text-align: center;
  background: #f9f9f9;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}
#box #content,
#box1 #content {
  position: absolute;
  left: 0;
  top: 50px;
  height: 490px;
  padding: 30px;
  overflow: auto;
  *overflow-y: auto;
  background: #f9f9f9;
}
#paymentHistory #paymentHistoryContent {
  position: absolute;
  left: 0;
  top: 50px;
  height: 490px;
  overflow: auto;
  *overflow-y: auto;
  background: #fff;
}
#box #content p,
#box1 #content p {
  color: #333333;
  font-size: 14px;
  line-height: 20px;
  margin-top: 10px;
  font-family: Arial, Helvetica, sans-serif;
}
#box #content .red,
#box1 #content .red {
  color: #c14925;
  font-weight: bold;
}
